<!-- 扫码结果页 -->
<template>
	<view class="scan-result">
		<!-- 大背景 -->
		<easy-loadimage :path="path"  imageClass="scan-result-bg" mode="widthFix"  :image-src="fileBaseUrl+'bfxn_scan_result_01.png'"></easy-loadimage>
		<!-- 内容部分 -->
		<view class="scan-result-body" v-if="type===1">
			<!-- icon -->
			    <view class="srb-i-text">
					奖励您
			    	<text class="srb-i-text-num">{{data.inc_num}}</text>
					个积分
			    </view>
			<!-- tips -->
			<view class="tips-msg">
				可在彬纷享礼小程序-积分商城中查看
			</view>
			<!--xie yi-->
			<view class="xy-text-list">
				<view class="xtl-item">积分奖励及积分活动由平台方提供，"1元享红牛"活动  </view>
				<view class="xtl-item">主办方不提供任何保证，且不承担任何法律责任，</view>
				<view class="xtl-item">您可以自行决定是否参与积分相关活动。</view>
			</view>
			<!-- body背景 -->
			<easy-loadimage :path="path"  imageClass="srb-bg" mode="widthFix"  image-src="https://file.y1b.cn/public/img/bfxn/202103/bfxn_scan_result_210501_2.png"></easy-loadimage>
		    
		</view>
		<view class="scan-result-body" v-if="type===2">
			<!-- icon -->
			<view class="srb-i">
				<easy-loadimage  :path="path"  imageClass="srb-i-icon" mode="widthFix" :image-src="data.icon"></easy-loadimage>
			</view>
			<!-- tips -->
			<view class="tips-msg">
				{{data.name}}
			</view>
			<!-- body背景 -->
			<easy-loadimage :path="path"  imageClass="srb-bg" mode="widthFix"  image-src="https://file.y1b.cn/public/img/bfxn/202103/bfxn_scan_result_210501_2.png"></easy-loadimage>
		</view>
		<!-- 继续扫码按钮 -->
		<view class="go-scan" @click="goShopMall">
			<view class="go-scan-text">前往兑换</view>
			<easy-loadimage :path="path"  imageClass="go-scan-bg" mode="widthFix"  :image-src="fileBaseUrl+'bfxn_scan_result_04.png'"></easy-loadimage>
		</view> 
		<!-- 积分规则 -->
		<view class="jf-details" v-if="type === 1" @click="goAgreementLook">
			<text class="jf-details-text">积分规则</text><image  class="jf-details-icon" src="../static/tips.png"></image>
		</view>
	</view>
</template>

<script>
	import {baseUrl} from '@/utils/http.js'
	import {mapActions,mapGetters} from 'vuex'
	export default {
		onLoad(option) {
			this.data = JSON.parse(decodeURIComponent(option.data))
			this.type = Number(option.type)
			this.path += 'type='+option.type+'&data='+option.data
			if(this.type === 1)this.getUserInfo()//更新用户信息 为了更新积分数量
		},
		 data(){
			 return {
				 data:{name:'',end_time:""},
				 type:1,//1积分  2卡劵
				 fileBaseUrl:'https://file.y1b.cn/public/img/bfxn/202103/',
				 path:'/pages/personal/scanResult/index?'
			 }
		 },
		 computed:{
			...mapGetters(['token']) 
		 },
		 methods:{
			 ...mapActions({
				 getUserInfo: 'login/getUserInfo'
			 }),
			 goShopMall(){
				this.$router.navigateTo({
					url:'/pages/webview/webview?link=' + encodeURIComponent(baseUrl+'/api/txjifen/txlogin?way=2&token='+this.token+'&random='+Date.now())
				})
			 },
			 gowebview(){
				if(!this.data.url) return this.$router.redirectTo({
					url:'/pages/personal/welfare/index'
				})
				//正常跳转
				this.$router.navigateTo({
					url:'/pages/webview/webview?link='+encodeURIComponent(this.data.url)
				}) 
			 },
			 goAgreementLook(){
				uni.navigateTo({
					url: '/pages/webview/webview?link='+encodeURIComponent('https://txc.y1b.cn/index/index/creditsrule.html')
				})
			 }
		 }
	}
</script>

